.video_content{
      width: 100%;
      height: auto;
}

.video_content h2{
      width: 100%;
      text-align: center;
      color: var(--dark-100);
      font-size: 1.6rem;
}

.video_box{
      width: 366px;
      height: 207px;
      background: var(--dark-200);
      margin-top: 1rem;
      position: relative;
}

.video_box:before{
      width: 70%;
      height: 50px;
      border-radius: 50%;
      background: #000;
      bottom: -25px;
      left: 15%;
      filter: blur(25px);
      z-index: -1;
      opacity: .6;
}

.video_box iframe{
      border: none;
}