.friends_title{
      height: 32px;
      display: flex;;
      align-items: center;
      justify-content: center;
      width: 100%;
}

.friends_title h2{
      position: relative;
      background: var(--theme);
      padding: .5rem 1.5rem;
      border-radius: 999px;
      font-size: 1.4rem;
}

.friends_title h2:before,
.friends_title h2:after{
      width: 14px;
      height: 14px;
      background: var(--second);
      border-radius: 50%;
      top: calc(50% - 7px);
}

.friends_title h2:before{
      left: -24px;
}

.friends_title h2:after{
      right: -24px;
}

.friends_slider{
      width: 280px;
      height: 95px;
      margin-top: 2.5rem;
      margin-left: calc(50% - 140px);
}