.content{
      width: 860px;
      height: 940px;
      padding: 3rem;
      background: linear-gradient(rgba(255,255,255,0), rgba(255,255,255,.2));
      position: relative;
}

.content:before{
      width: 490px;
      height: 490px;
      background: var(--gradient-45);
      border-radius: 50%;
      bottom: -245px;
      left: -300px;
      z-index: -1;
}

.content_inner__page{
      width: 100%;
      height: 100%;
      position: relative;
      background: var(--grys-100);
      box-shadow: 0 0 30px rgba(0,0,0,.5);
      overflow: hidden;
}

.content_inner__page:before,
.content_inner__page:after{
      border-radius: 50%;
      top: 100%;
      left: 100%;
      transform: translate(-50%, -50%);
}

.content_inner__page:before{
      width: 270px;
      height: 270px;
      background: var(--second);
}

.content_inner__page:after{
      width: 200px;
      height: 200px;
      background: var(--theme);
}

.wrapper_banner{
      width: 100%;
      height: 300px;
      position: absolute;
}

.banner_content{
      width: 100%;
      height: 100%;
      display: flex;
      position: relative;
      background: var(--light);
}

.banner_content img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
      opacity: .5;
}

.banner_content:before{
      width: 100%;
      height: 100%;
      background: linear-gradient(rgba(200,200,200,.5), var(--grys-100));
      top: 0;
      left: 0;
      z-index: 2;
}

.container{
      width: 100%;
      height: 100%;
      display: grid;
      grid-template-columns: 350px 370px;
      padding: 3rem;
      gap: 2rem;
      position: relative;
      z-index: 10;
}

.wrapper_user{
      width: 100%;
      height: 410px;
      margin-top: 5rem;
      background: var(--theme);
      border-radius: 3rem;
      position: relative;
      box-shadow: 4rem 4rem 30px rgba(0,0,0,.2);
}

.wrapper_user:before{
      width: 300px;
      height: 100%;
      background: var(--light);
      border-radius: 3rem;
      top: 0;
      right: 0;
}

.user_heart__icon{
      position: absolute;
      width: 70px;
      height: 70px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      background: var(--theme);
      left: 32px;
      bottom: 8rem;
}

.user_heart__icon *{
      font-size: 2.8rem;
      color: var(--light);
}

.user_header{
      width: 100%;
      height: 180px;
      display: flex;
      align-items: center;
      gap: 1rem;
      position: relative;
      transform: translateX(-1rem);
      padding-top: 2rem;
}

.user_avatar, 
.user_avatar img{
      width: 160px;
      height: 160px;
      border-radius: 1rem;
}

.user_avatar{
      display: flex;
      box-shadow: 2rem 2rem 25px rgba(0,0,0,.3);
}

.user_nav__menu{
      width: 38px;
      height: 160px;
      display: flex;
      flex-direction: column;
      gap: .8rem;
      align-items: center;
      justify-content: center;
}

.user_nav__menu a{
      width: 38px;
      height: 38px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      box-shadow: 0 0 0 .2rem var(--second) inset;
}

.user_nav__menu a *{
      font-size: 2rem;
      color: var(--theme);
}

.user_content{
      margin-top: 1.5rem;
      position: relative;
}

.user_title{
      display: inline-block;
      padding-left: 1.5rem;
      text-align: right;
}

.user_title h1{
      color: var(--dark-100);
      font-size: 4rem;
      line-height: 3rem;
}

.user_title strong{
      color: var(--second);
      font-size: 3rem;
}

.user_footer{
      width: 300px;
      height: auto;
      margin-left: 5rem;
      margin-top: 1rem;
      color: var(--grys-200);
      position: relative;
}

.user_footer_details{
      padding-left: 6rem;
}

.user_footer_details span{
      font-size: 1.4rem;
}

.user_location{
      display: flex;
      height: 28px;
      align-items: baseline;
      margin-top: 1rem;
}

.user_location *{
      font-size: 2rem;
      color: var(--second);
}

.user_location label{
      font-size: 1.4rem;
}

.user_footer__social{
      width: 100%;
      height: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 1rem;
      gap: 1rem;
}

.about_user{
      margin-top: 2.5rem;
      width: 100%;
      height: auto;
      min-height: 100px;
      max-height: 150px;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: var(--alpha) var(--theme);
}

.about_user p{
      color: var(--dark-100);
      font-size: 1.4rem;
}

.wrapper_friends{
      width: 100%;
      height: 140px;
      padding: 0 2rem;
      margin-top: 1rem;
}





.container_col:last-child{
      position: relative;
      z-index: 100;
}

.wrapper_floating{
      width: 250px;
      height: 350px;
      position: absolute;
      top: 4rem;
      left: 0;
      transform: translateX(-75px);
      z-index: 10;
}

.wrapper_floating:after{
      width: 170px;
      height: 2px;
      background: var(--light);
      z-index: 9;
      transform: translate(55%, 200%) rotate(-50deg);
      top: 20%;
      left: 0;
}

.floating_icon{
      width: 38px;
      height: 38px;
      border-radius: 50%;
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--light);
      top: 0;
      right: 0;
      box-shadow: 0 0 0 4px rgba(255,255,255,.3);
      z-index: 10;
}

.floating_icon *{
      font-size: 1.6rem;
      color: var(--theme);
}

.floating_data{
      position: absolute;
      top: 123px;
      left: 35px;
      width: 170px;
      height: 170px;
      background: var(--light);
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
}

.floating_data:before,
.floating_data:after{
      left: 50%;
      border-radius: 50%;
      transform: translateX(-50%);
}

.floating_data:before{
      width: 240px;
      height: 240px;
      background: var(--theme);
      top: -2rem;
      z-index: -2;
      box-shadow: 0 -2rem rgba(255,255,255,.4);
}

.floating_data:after{
      width: 210px;
      height: 210px;
      background: var(--second);
      top: -1rem;
      z-index: -1;
}

.floating_data h1{
      color: var(--theme);
      font-size: 5.5rem;
      line-height: 4.5rem;
}

.floating_data span{
      color: var(--dark-100);
      text-transform: uppercase;
      font-size: 4rem;
      line-height: 3.5rem;
      font-weight: 200;
}

.wrapper_relation{
      margin-top: 4.5rem;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
}

.relation_text{
      width: 190px;
      height: 240px;
}

.relation_text h2{
      color: var(--dark-100);
      font-size: 2.4rem;
}

.relation_about{
      width: 100%;
      height: 205px;
      margin-top: 1rem;
      overflow-y: auto;
      overflow-x: hidden;
      scrollbar-width: thin;
      scrollbar-color: var(--alpha) var(--theme);
}

.relation_about p{
      font-size: 1.4rem;
      color: var(--dark-200);
}

.relation_data{
      width: 100%;
      height: 160px;
      display: flex;
      gap: 1rem;
      align-items: center;
      justify-content: flex-end;
      margin-top: 1rem;
}

.relation_data__info{
      flex: 1;
      height: 160px;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      justify-content: flex-end;
}

.relation_data__info h2{
      color: var(--dark-100);
      font-size: 3.5rem;
}

.relation_data__info a{
      padding: .5rem 1.5rem;
      background: var(--theme);
      border-radius: 999px;
      color: var(--light);
      text-transform: uppercase;
}

.relation_data__avatar,
.relation_data__avatar img{
      width: 160px;
      height: 160px;
      border-radius: 1rem;
}

.relation_data__avatar{
      display: flex;
      box-shadow: 20px 20px 25px rgba(0,0,0,.2);
      flex-shrink: 0;
}

.relation_time{
      margin-top: 1rem;
      width: 100%;
}

.relation_time__inner{
      width: 200px;
      height: 40px;
      display: flex;
      flex-direction: column;
      align-items: center;
}

.relation_time__inner span{
      color: var(--dark-100);
      font-size: 1.4rem;
      word-wrap: break-word;
}

.relation_time_range{
      width: 100%;
      height: 3px;
      border-radius: 3px;
      background: var(--second);
      margin-top: 1rem;
      position: relative;
}

.relation_time_range:before,
.relation_time_range:after{
      width: 14px;
      height: 14px;
      border-radius: 50%;
      background: var(--theme);
      top: calc(50% - 7px);
}

.relation_time_range:before{
      left: 1rem;
}

.relation_time_range:after{
      right: 1rem;
}

.wrapper_video{
      width: 100%;
      height: auto;
      position: relative;
      margin-top: 5rem;
}