.friend_card,
.friend_card a{
      width: 80px;
      height: 95px;
      position: relative;
}

.friend_card a:before{
      content: attr(data-tooltip);
      color: var(--light);
      width: 70px;
      height: 26px;
      line-height: 26px;
      background: var(--theme);
      border-radius: 3px;
      left: 5px;
      bottom: 5px;
      text-align: center;
      box-shadow: 5px 5px 5px rgba(0,0,0,.1);
}

.friend_card__img,
.friend_card__img img{
      width: 80px;
      height: 80px;
      border-radius: .6rem;
      overflow: hidden;
}

.slick-slide{
      margin: 0 5px;
}

.slick-list{
      height: 120px;
      margin: 0 5px;
}

.slick-slider{
      position: relative;
}

.slick-arrow{
      width: 36px;
      height: 36px;
      position: absolute;
      top: 35%;
      border-radius: 50%;
}

.slick-arrow:before{
      width: 10px;
      height: 10px;
      left: 50%;
      top: 50%;
      color: transparent;
      border-top: solid 2px var(--theme);
      border-left: solid 2px var(--theme);
}

.slick-arrow.slick-prev:before{
      transform: translate(-50%, -50%) rotate(-45deg);
}

.slick-arrow.slick-next:before{
      transform: translate(-50%, -50%) rotate(135deg);
}


.slick-arrow.slick-prev{
      left: -3rem;
}

.slick-arrow.slick-next{
      right: -2rem;
}