.wrapper_online{
      height: 38px;
      display: flex;
      gap: .5rem;
      align-items: flex-start;
}

.online_avatar,
.online_avatar img{
      width: 38px;
      height: 38px;
      border-radius: 50%;
}

.online_avatar{
      background: var(--grys-200);
      position: relative;
      display: flex;
}

.online_dots{
      width: 14px;
      height: 14px;
      border-radius: 50%;
      background: var(--light);
      position: absolute; 
      top: 0;
      right: 0;
}

.online_dots.green{
      background: var(--green);
}

.online_dots.red{
      background: var(--red-200);
}

.wrapper_online span{
      padding: .5rem 1.5rem;
      background: var(--theme);
      border-radius: 999px;
      text-transform: uppercase;
}