      border-radius: 50%;
      box-shadow: 1px 1px 1px rgba(0,0,0,.08);
      display: flex;
      align-items: center;
      justify-content: center;
}

.btn_social *{
      font-size: 1.8rem;
      color: var(--dark-100);
}